#drum-machine {
  text-align: center;
  margin-top: 50px;
}

.pad-bank {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.drum-pad {
  background: #4caf50;
  color: white;
  font-weight: bold;
  padding: 20px;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
}

.drum-pad:active {
  background: #45a049;
}
